<template>
    <div> SignOut </div>
</template>

<script>
    import { mapActions } from 'vuex'
    export default {
        methods: {
            ...mapActions([
                'signOut'
            ]),
            async SignOUT () {
                await this.signOut()
                this.$router.push({ name: 'Home' })
                this.$swal({
                    title: 'ອອກຈາກລະບົບແລ້ວ',
                    type: 'info',
                    toast: true,
                    position: 'top-end',
                    showConfirmButton: false,
                    timer: 3000
                })
            }
        },
        created () {
          this.SignOUT()
        }
    }
</script>
